body, a, ul, li {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

:root {
  --main-color: #00acdc;
}

a {
  color: inherit;
  text-decoration: none;
}

html {
  font-family: Roboto, Arial, sans-serif;
  font-size: 16px;
  font-style: normal;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  display: flex;
}

.nav__item {
  color: #000;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__item:hover {
  color: var(--main-color);
}

.nav__link {
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin: auto;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  display: flex;
  position: relative;
}

.is-active {
  color: var(--main-color);
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
